document.getElementById("numbers").onclick = function numbers() {
    let number = prompt("–ü–æ–∂–∞–ª—É–π—Å—Ç–∞,–≤–µ–¥–∏—Ç–µ —Ç—Ä—ë—Ö–∑–Ω–∞—á–Ω–æ–µ —á–∏—Å–ª–æ", "");
    let reverse = 0;
    while (number > 0) {
        let digit = number % 10;
        reverse = (reverse * 10) + digit;
        number = parseInt(number / 10);
    }
    alert("–í–∞—à–µ —á–∏—Å–ª–æ –Ω–∞–æ–±–æ—Ä–æ—Ç " + reverse);
}

document.getElementById("deposit").onclick = function deposit() {
    const deposit = prompt("–ü–æ–∂–∞–ª—É–π—Å—Ç–∞,–≤–µ–¥–∏—Ç–µ —Å—É–º–º—É, –∫–æ—Ç–æ—Ä—É—é –ø–ª–∞–Ω–∏—Ä—É–µ—Ç–µ –ø–æ–ª–æ–∂–∏—Ç—å –Ω–∞ –¥–µ–ø–æ–∑–∏—Ç", "");
    const interestRate = (0.05);
    let accruedInterest = ((deposit * interestRate * 61) / 365);
    alert(`–°—É–º–º–∞ –Ω–∞—á–∏—Å–ª–µ–Ω–Ω—ã—Ö –ø—Ä–æ—Ü–µ–Ω—Ç–æ–≤ –∑–∞ 2 –º–µ—Å—è—Ü–∞ —Å–æ—Å—Ç–∞–≤–∏—Ç` + accruedInterest);

}